import QQMapWX from '@/common/utils/qqmap-wx-jssdk.min.js'
import modal from "@/components/modal/modal"
import tuiIcon from "@/components/icon/icon"
import md5 from "@/common/utils/md5.js"
import tuiCountdown from "@/components/countdown/countdown"
import {
	ajax,
	getSecret
} from '@/common/ajax/ajax.js'
export default {
	onShareAppMessage(res) {
		return {
			title: '熙美诚品',
			path: 'pages/index/index',
			success: (res) => {
				console.log(res);
			},
			fail: (res) => {
				console.log(res);
			}
		}
	},
	components: {
		modal,
		tuiIcon,
		tuiCountdown
	},
	data() {
		return {
			userProvider:'',
			scrollTop: 0,
			getheight: '',
			specialImgHeight: '',
			storeName: '总部旗舰店',
			address: '',
			city: '',
			id: '',
			secret: {},
			showHeader: true,
			afterHeaderOpacity: 1, //不透明度
			headerPosition: 'fixed',
			headerTop: null,
			statusTop: null,
			nVueTitle: null,
			currentSwiper: 0,
			// 轮播图片
			swiperList: [],
			// 活动图片
			actionBanner: [],
			// 分类菜单
			categoryList: [],
			// 限时抢购
			seckillList: [],
			// 砍价
			bartainlist: [],
			// 拼团
			assembleList: [],
			// 活动图片
			imgList: [{
					img: '../../static/img/01.png',
					key: '1'
				},
				{
					img: '../../static/img/02.png',
					key: '2'
				},
				{
					img: '../../static/img/03.gif',
					key: '3'
				}
			],
			seckillTime: 0,
			bargainList: [],
			Promotion: [],
			//猜你喜欢列表
			productList: [],
			// 活动
			specialList: [],
			loadingText: '到底了',
			amapPlugin: null,
			// 定位弹窗
			modalShow: false,
			storeItems: [{
				store_name: '总部旗舰店',
				desc: '由总部统一发货',
				store_code: '10001'
			}],
			// 门店编码
			store: '10001',
			hint: '熙美诚品',
			popup: {},
			// 向左滑动距离
			scrollLeft: 0,
			// 弹窗高度
			popHeight: 0,
			addShow: true,
			// 品牌
			traitList: [],
			traitListImg: [
				'/static/img/pinpai.png',
				'/static/img/baoyou.png',
				'/static/img/jisu.png'
			]
		};
	},
	onPageScroll(e) {
		this.scrollTop = e.scrollTop;
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop >= 0 ? "fixed" : "absolute";
		this.headerTop = e.scrollTop >= 0 ? null : 0;
		this.statusTop = e.scrollTop >= 0 ? null : -this.statusHeight + 'px';
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		this.getgoods();
		this.getassembleList();
		this.getBargin();
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},

	onLoad() {
		this.getbig();
		this.getgoods();
		this.getassembleList();
		this.getLocation();
		this.getBargin();
		this.userProvider=this.$xm.userProvider();
		if(this.userProvider!='weixin'){
			uni.login({
				success: (res) => {
					console.log(res)
					if (res.code) {
						var code = res.code
						
						uni.getUserInfo({
							success: (res) => {
								console.log(res)
								let data = {
									encryptedData: res.encryptedData,
									iv: res.iv
								}
								this.$xm.post('/Index/getUnionid', data, (res) => {
									const user = uni.getStorageSync('user');
									user.unionid=res.unionid;
									uni.setStorage({
										key: 'user',
										data: user,
										success: (res) => {
											console.log(res);
										}
									});
								})
								uni.setStorage({
									key: 'userInfo',
									data: res.userInfo,
									success: (res) => {
										console.log(res);
									}
								});
			
							},
							fail: res => {
								console.log(res)
								// 获取失败的去引导用户授权 
							}
						})
			
					} else {}
				}
			})
		}
	},
	onShow() {
		console.log(uni.getStorageSync('store'));
		if (uni.getStorageSync('store')) {
			this.storeName = uni.getStorageSync('store').store_name;
			this.store = uni.getStorageSync('store').store_code
			this.getgoods();

		}

	},
	filters: {
		toFixed(x) {
			return parseFloat(x).toFixed(1);
		}
	},
	methods: {
		// 获取商品总分类
		getbig() {
			let timestamp = (new Date()).getTime();
			let secret = md5.hex_md5("XM#" + md5.hex_md5(String(timestamp)) + "@secret");
			let data = {
				store: this.store,
				secret: timestamp,
				timestamp: secret
			}
			ajax({
				url: '/Procate/getBig',
				method: 'post',
				data: data,
				success: (res) => {
					this.categoryList = res.data.splice(1, 10);
				}
			})
		},
		getLocation() {
			uni.getLocation({
				success: (res) => {
					this.getCity(res.latitude, res.longitude) //获取当前所在城市						
				},
				fail: (res) => {
					console.log(res);

				}
			})
		},
		// 获取定位城市
		getCity(latitude, longitude) {
			let demo = new QQMapWX({
				key: 'LVHBZ-SOY6W-Z5URP-OC34Q-CLWZH-Y6BCX' // 开发密钥（key）
			});
			demo.reverseGeocoder({
				location: {
					latitude: latitude,
					longitude: longitude
				},
				success: (res) => {
					console.log(res);
					this.city = res.result.ad_info.city;
					let latitude = res.result.location.lat;
					let longitude = res.result.location.lng;
					this.address = res.result.address
					uni.setStorageSync('local', res.result.address);
					// this.getgoods()
					let params = {
						city: this.city,
						from: latitude + ',' + longitude
					}
					this.$xm.post('/Index/getStoreList', params, (res) => {
						this.storeItems = res.data;
						uni.setStorageSync('storeList', res.data)

					})
				},
				fail: function(res) {
					console.log(res)

				},
				complete: function(res) {

				}
			});
		},
		// 获取拼团
		getassembleList() {
			this.$xm.post('/groupbuy/getlist', '', (res) => {
				this.assembleList = res.list.splice(0, 2);
				this.assembleList.forEach((ele) => {
					ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300';
				})
			})
		},
		// 获取砍价
		getBargin() {
			this.$xm.post('/bargain/getlist', "", res => {
				let list = res.list
				if (list) {
					list.forEach((ele) => {
						ele.pro_img = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300';
					})
				}
				this.bartainlist = list;
			})
		},
		// 跳转到砍价
		toBargain() {
			uni.navigateTo({
				url: '../seckill/bargain/bargain'
			})
		},
		// 获取商品
		getgoods() {
			this.swiperList = []
			let timestamp = (new Date()).getTime();
			let secret = md5.hex_md5("XM#" + md5.hex_md5(String(timestamp)) + "@secret");
			let data = {
				store: this.store,
				secret: timestamp,
				timestamp: secret
			}
			ajax({
				url: '/Index/getInfo',
				method: 'post',
				data: data,
				success: (res) => {
					let result = res.data;
					this.traitList = result.trait;

					this.popup = result.popup;
					this.popHeight = this.popup.height;
					if (uni.getStorageSync('store')) {
						this.modalShow = false;
					} else {
						if (result.popup.img != '') {
							this.modalShow = true;
						} else {
							this.modalShow = false;
						}
					}
					this.getheight = result.banner[0].height + 'rpx';
					this.specialImgHeight = result.special.height + 'rpx';
					this.hint = result.hint
					uni.setNavigationBarTitle({
						title: this.hint
					});
					this.swiperList = result.banner;
					this.swiperList.map(ele => {
						ele.img = result._URL_ + ele.img
					})
					this.actionBanner = result.img;

					result.faddish.forEach((ele) => {
						ele.itemcount = 0;
						ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300';

					})
					this.Promotion = result.faddish;
					this.bargainList = result.bargain;
					this.productList = result.rem;
					this.bargainList.forEach((ele) => {
						ele.itemcount = 0;
						ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300';
					})
					this.productList.forEach((ele) => {
						ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300';
						ele.itemcount = 0;
						if (ele.pro_name.length > 8) {}
					})
					if (result.special.goods) {
						result.special.goods.map((ele) => {
							ele.itemcount = 0;
							ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300';
						})
					}

					this.specialList = result.special;
					this.seckillList = result.seckill.next.splice(0, 2);
					// this.seckillList = result.seckill.next;
					this.seckillList.forEach((ele) => {
						ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300';
					})
					if (result.seckill.next.length > 0) {
						this.seckillTime = parseInt(result.seckill.next[0].etime) - Math.round(new Date() / 1000);
					}

				}
			})



		},

		scroll(e) {
			this.scrollLeft = e.detail.scrollLeft;
		},
		//搜索跳转
		toSearch() {
			uni.navigateTo({
				url: '../search/search/search'
			})

		},
		// 跳转宠物页
		toPet() {
			// uni.navigateTo({
			// 	url: '../pet/pet/pet'
			// })
		},
		//轮播图跳转
		toSwiper(e) {
			let action = e.src.split('@');

			switch (action[0]) {
				case 'product':
					uni.navigateTo({
						url: '../goods/goods?gid=' + action[1]
					})
					break;
				case 'news':
					uni.navigateTo({

					})
					break;
				case 'sort':

					break;
				case 'player':
					uni.navigateTo({
						url: "plugin-private://wx2b03c6e691cd7370/pages/live-player-plugin?room_id=" + action[1]
					})
					break;
				case 'open':
					uni.navigateTo({
						url: "../webViwe/webViwe?url=" + action[1]
					})
					break;
				case 'cate':
					uni.setStorageSync('cateSelect', action[1]);
					uni.switchTab({
						url: "../tabBar/category/category"
					})
					break;
				default:

			}
		},
		// 活动跳转
		toPage(e) {
			let action = e.url.split('@');
			console.log(action)
			switch (action[0]) {
				case 'product':
					uni.navigateTo({
						url: '../goods/goods?gid=' + action[1]
					})
					break;
				case 'news':
					// uni.navigateTo({

					// })
					break;
				case 'sort':

					break;
				case 'player':
					uni.navigateTo({
						url: "plugin-private://wx2b03c6e691cd7370/pages/live-player-plugin?room_id=" + action[1]
					})
					break;
				case 'open':
					uni.navigateTo({
						url: '../webViwe/webViwe/webViwe?url=' + action[1]
					})
					break;
				case 'cate':
					uni.setStorageSync('cateSelect', action[1]);
					uni.switchTab({
						url: "../tabBar/category/category"
					})
					break;
				default:

			}

		},
		toProduct(e) {
			uni.navigateTo({
				url: '../goods/goods-list/product/product?key=' + e
			})
		},
		//分类跳转
		toCategory(e) {
			uni.setStorageSync('cateSelect', e);
			uni.switchTab({
				url: '../tabBar/category/category'
			})

		},
		//商品跳转
		toGoods(e) {
			uni.navigateTo({
				url: '../goods/goods?gid=' + e.id
			});
		},
		// 秒杀跳转
		toSeckill() {
			uni.navigateTo({
				url: '../seckill/seckill/seckill'
			})
		},
		// 拼团跳转
		toGroup() {
			uni.navigateTo({
				url: '../seckill/group/group'
			})
		},
		//轮播图指示器
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		},
		tobuy(e) {
			// if(this.addShow){
			// 	e.itemcount++;
			// }
			this.id = e.id;
			this.joinCart();
		},
		// 加入购物车
		joinCart() {
			let data = {
				proid: this.id,
				pronum: 1,
			};
			this.$xm.post('/Cart/add', data, (res) => {
				let result = res.msg;
				if (res.s == 1) {
					this.addShow = true;
					uni.showToast({
						title: result
					});
				} else if (res.s == 0) {
					this.addShow = false;
					uni.showToast({
						title: result,
						icon: 'none'
					});
				}
				this.$xm.post('/Cart', '', res => {
					uni.setStorage({
						key: 'cart',
						data: res.count,
						success: (res) => {
							uni.getStorage({
								key: 'cart',
								success: (res) => {
									let count = res.data;
									if (res.data !== null) {
										count = count.toString();
										uni.setTabBarBadge({
											index: 2,
											text: count
										})
									}
								}
							})
						}
					})
				})
			})

		},
		//跳转确认订单页面
		toConfirmation() {
			uni.navigateTo({
				url: '../order/confirmation'
			})
		},
		// 跳转到所有门店页面
		toStore() {
			uni.navigateTo({
				url: '../store/store/store'
			})
		},
		// 跳转活动
		toactivity() {

		}
	}
};
